import {LoadingController, ModalController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { EmailUserViewModel } from "../../Model/ViewModel/EmailUserViewModel";
import { GlobalComponent } from "../../components/global/global";

/**
 * Generated class for the EmailUserModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-email-user-add-edit-modal",
  templateUrl: "email-user-add-edit-modal.html"
})
export class EmailUserAddEditModalPage {
  item: any;
  mode: string;
  CanEditEmail: boolean;
  title:string;
  EmailUser_type:string;
  sql_help: string = `
  標題與內文-變數說明：
  {SSIS_JOB_NO}=作業編號,
  {JOBDESC}=作業說明,
  {ERR_MSG}=異常訊息,
  {ERR_MSG_SEQ}=異常訊息編號,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public global:GlobalComponent,


  ) {
    this.item = new EmailUserViewModel();
    this.item.email_id=navParams.data.item.email_id;
    this.item.email_desc=navParams.data.item.email_desc;
    this.item.fromaddress=navParams.data.item.fromaddress;
    this.item.toaddress=navParams.data.item.toaddress;
    this.item.ccaddress=navParams.data.item.ccaddress;
    this.item.bccaddress=navParams.data.item.bccaddress;
    this.item.subject=navParams.data.item.subject;
    this.item.body=navParams.data.item.body;
    this.item.parameter_column_group=navParams.data.item.parameter_column_group;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.item.send_status_key=navParams.data.item.send_status_key;
    this.item.send_status=navParams.data.item.send_status;
    this.item.can_rerun=navParams.data.item.can_rerun;

    this.CanEditEmail = navParams.data.CanEditEmail;
    this.mode = navParams.data.mode;
    this.title="收件者分類";
  }
  SelectSendStatus() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.send_status_key,
      code_type: "071"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.send_status_key = select_data.code_key;
      this.item.send_status = select_data.code_desc;
    });

    modal.present();
  }
  SelectParameterGroup() {
    let modal = this.modalCtrl.create("ParameterGroupSelectModelPage", {
      select_parameter_column_group: this.item.parameter_column_group,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.parameter_column_group = select_data.parameter_column_group;
    });

    modal.present();
  }
  Help() {
    this.global.createLoader();

    this.global.loading.present().then(() => {
      this.global.showMessage("SQL語法說明", this.sql_help);
    });
  }
  openNavParameterColumnPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("ParameterColumnPage", { item: item });
    });
    this.global.dismissLoading();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad EmailUserModalPage");
  }
}



