export class EmailUserViewModel {
  /// <summary>
///  郵件編號
/// <summary>
public email_id: string;
/// <summary>
///  郵件說明
/// <summary>
public email_desc: string;
/// <summary>
///  郵件說明(含代碼)
/// <summary>
public email_full_desc: string;
/// <summary>
///  寄件者清單
/// <summary>
public fromaddress: string;
/// <summary>
///  收件者清單
/// <summary>
public toaddress: string;
/// <summary>
///  收件者副本清單
/// <summary>
public ccaddress: string;
/// <summary>
///  隱密收件者清單
/// <summary>
public bccaddress: string;
/// <summary>
///  標題
/// <summary>
public subject: string;
/// <summary>
///  內文
/// <summary>
public body: string;
/// <summary>
///  參數欄位群組
/// <summary>
public parameter_column_group: string;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
///  寄發狀態主鍵
/// <summary>
public send_status_key: string;
/// <summary>
///  重複執行註記
/// <summary>
public can_rerun: boolean;

/// <summary>
///  寄發狀態
/// <summary>
public send_status: string;

}



